$break-90: 450px;
$break-80: 400px;
$break-70: 350px;
$break-60: 300px;

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

html {
  body {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: hsl(222, 11%, 18%);
    color: white;
  }

    #main-logo {
      height: 30%;
      max-width: 100%;
      margin-bottom: 10%;
      flex: none;
      margin-top: 10%
    }
    .center-links {
      width: 80%;
      max-width: 45em;
      height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex: none;
      margin-top: 5%;
      margin-bottom: 25%;
      a {
        text-decoration: none;
        color: inherit;
        font-weight: bold;
        font-size: 100%;
        height: 50px;
        width: 32%;
        margin: auto;
        line-height: 50px;
        text-align: center;
        box-sizing: border-box;
        border: 2px solid white;
        @media only screen and (max-width: $break-90) {
          font-size: 90%;
        }
        @media only screen and (max-width: $break-80) {
          font-size: 80%;
        }
        @media only screen and (max-width: $break-70) {
          font-size: 70%;
        }
        @media only screen and (max-width: $break-60) {
          font-size: 60%;
        }
      }
    }
  }
